import React from "react";
import { image } from "faker";
const getImages = () => {
  let images = [];
  for (var i = 0; i < Math.floor(Math.random() * 100); i++) {
    images.push(
      <img
        src={image.avatar()}
        style={{ flex: 1, height: "10vh", minWidth: 100 }}
      />
    );
  }
  return images;
};
export const gallery = getImages();
